* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background-color: #eff4f7;
  height: 100%;
}

#root,
.container,
body,
html {
  height: 100%;
  justify-content: center;
}

.loader {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.movies {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 70px 50px 50px;
  width: 100%;
}
.movies .movie {
  width: 45%;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  background-color: white;
  margin-bottom: 70px;
  font-weight: 300;
  padding: 20px;
  color: #adaeb9;
  border-radius: 5px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie img {
  top: -50px;
  position: relative;
  width: 100%;
  max-width: 150px;
  margin-right: 30px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie .moive__year,
.movie .movie__title {
  margin: 0;
  font-size: 300;
}

.movie .movie__title {
  margin-bottom: 5px;
  font-size: 24px;
  color: #2c2c2c;
}

.movie .movie__genre {
  list-style: none;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  margin: 5px 0;
}

.movie__genre li {
  margin-right: 10px;
  font-size: 14px;
}

.about {
  font-weight: 300;
  flex-wrap: wrap;
  border-radius: 5px;
  font-size: 20px;
  width: 100%;
  max-width: 400px;
  margin: 100px auto 0;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  background-color: #fff;
  box-shadow: 0 13px 27px -5px rgb(50 50 93 / 25%),
    0 8px 16px -8px rgb(0 0 0 / 30%), 0 -6px 16px -6px rgb(0 0 0 / 3%);
}
